.page{
    display: flex;
    flex-direction:column;
    min-height: 100vh;
}

.header{
    width: 100%;
    background-color: orange;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
}
    .header>a{
        color: white;
        margin-right: 20px;
        text-decoration: none;
    }
    .header >a:hover{
        text-decoration: underline;
    }

    .header >a:active{
        text-decoration: underline;
        color: black;
        transition: 0;
    }
   
    /* .active{
        text-decoration: underline;
        color: rgb(2, 2, 2);
    } */

.footer{
    width: 100%;
    background-color: #585050;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    
}
.main{
  flex: 1;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 20px 0;

}
